.rs-header {
  display: flex;
  justify-content: space-between;
  border-bottom: 1px rgba(255, 255, 255, 0.1) solid;
  margin-bottom: 8px; }

.rs-collapse-btn {
  cursor: pointer;
  font-size: 12px; }

.rs-fps-counter {
  font-family: monospace;
  cursor: pointer;
  position: absolute;
  bottom: 0px;
  right: 2px;
  padding: 4px 8px;
  color: #aaa;
  font-size: 10px;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none; }

.rs-base {
  right: 10px;
  left: auto;
  top: 10px;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none; }
